export default {
  // 
  _success(value, $vm) {
    $vm.$Message.success(value);
  },

  // 
  _error(value, $vm) {
    $vm.$Message.error({
      content: value,
      duration: 4
    });
  },

  // 
  _warning(value, $vm) {
    $vm.$Message.$Message.warning({
      content: value,
      duration: 4
    });
  },

  // 
  _loading(value, $vm) {
    $vm.$Message.loading({
      content: value,
      duration: 30,
      closable: false
    });
  },

  // 
  _info(value, $vm) {
    $vm.$Message.info({
      content: value,
      duration: 4
    });
  },
}




